/* landing header */

.header-container{
  text-align: center;
  margin: 0;
}
.occupation{
  margin: 0;
}
.name-container{
  display: flex;
  justify-content: space-evenly;
  margin: 0 0 200px 0;
  padding: 100px 200px 400px 200px;

}
.mtn{
  position: absolute;
  z-index: -1;
  transform: translateX(-15%);
  opacity: 80%;
  height: 500px;
  
}
.fName{
  font-size: 80px;
  margin: 0 0 0 150px;
}
.mName{
  font-size: 80px;
  margin: 0 0 0 300px;
}
.lName{
  font-size: 80px;
  margin: 0 0 0 400px;
}


/* Text CSS */
h1{
  font-family: 'Merriweather', serif;
  font-weight: 50;
  font-size: 100px;
  color: #FFFFFD;
  margin: 0px 20px 150px 20px;
  text-shadow: 5px 5px 8px #6c6566;
}

.anchor {
  display: block;
  position: relative;
  top: -75px;
  visibility: hidden;
}

