/* ICONS */
.icon{
  height: 40px;
  color: #577273;
}


/* div css */
.h1-padding{
  padding-top: 100px;
 }

.section-container{
  padding: 20px;
  margin: 0px 0px 150px 0px;
}

.section-container__header{
  font-family: 'Shadows Into Light', cursive;
  text-shadow: 3px 3px 10px #6c6566;
  color: #43686c;
  font-size: 65px;
  font-weight: 10;
  display: flex;
  justify-content: center;

}

.page-padding{
  padding: 0 250px;
  margin: 0 auto;
}

.nav{
  font-family: 'Merriweather', serif;
  background-color: #274247;
  height: 60px;
  position: fixed;
  top: 0;
  width: 100%;
  display: flex;
  align-items: center;
  box-shadow: 0 0 px 0 rgb(0 0 0 / 20%);
}

.nav a{
  color: #FFFFFF;
  padding: 20px;
}


/* About Me CSS */
.about-me-container{
  display:flex;
  justify-content: center;
  flex-wrap: wrap;
  align-items: center;
  width: auto;
  padding: 50px 0 0 0;
}

.about-me-container p{
  font-family: 'Roboto', sans-serif;
  font-weight: 600;
  text-align: center;
  font-size: 20px;
  color: #544C4E;
  width: 500px;
  margin: 0px 10px 0px 10px;
}

.about-me-container__picture-container{
  display: flex;
  flex-direction: column;
}

.about-me-container__picture-container__icons{
  padding: 10px;
  display: flex;
  justify-content: center;
}

.about-me-container__picture-container__icons > a{
  padding: 10px;

}

.profilePic{
  border-radius: 3%;
  height: 300px;
  width: 300px;
  box-shadow: 0 4px 8px 0 rgb(0 0 0 / 20%), 0 6px 20px 0 rgb(0 0 0 / 19%);

}

/* Projects CSS */
.projectsBoard{
  display:flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: center;
  height: auto;
}

.project-card-container{
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  font: #544C4E;
  background-color: #f0f0f0;
  box-shadow: 0 4px 8px 0 rgb(0 0 0 / 20%), 0 6px 20px 0 rgb(0 0 0 / 19%);
  border-radius: 3%;
  overflow: hidden;
  height: 600px;
  width: 400px;
  margin: 50px 10px 50px 10px;
}

.project-card-container__photo{
  display: block;  
  height: 300px;
  width: 100%;
  overflow: hidden;
}

.project-card-container__photo img{
  width: 100%;
  display: flex;
  justify-content: center;
}

.project-card-container__body{
  font-family: 'Roboto', sans-serif;
  font-weight: 600;
  color: #544C4E;
  padding: 0px 20px 10px 20px;
}

.project-card-container__body__header{
  display: flex;
  justify-content: space-between;
  color:  #544C4E;
  font-size: 35px;
  font-weight: 700;
}

/* Tech Stack */
.tech-title{
  color:  #544C4E;
  font-size: 35px;
  font-weight: 700;
  padding: 0px 0px 50px 0px;
  
}

.tech-container_stack-table{
  display: flex;
  justify-content: center;
  margin: 50px 0 0 0;
}

.row{
  display: flex;
  justify-content: space-between;
}

.column{
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: 'Roboto', sans-serif;
  font-weight: 600;
  color: #544C4E;
  margin: 40px 100px 40px 100px;
}

.stack-icon-container{
  display: flex;
  justify-content: center;
  flex-direction: row;
  flex-wrap: wrap;
  width: 500px;
}

.stack-items{
  display: flex;
  flex-direction: column;
  align-items: center;
}


.stack{
  height: 150px;
  width: 150px;
}

/* Email */
.email-container{
  display: flex;
  justify-content: center;
  margin: 50px 0 0 0;

}


